<template>
  <div id="container">
    <el-menu :default-active="activeIndex" mode="horizontal" @select="handleSelect">
      <el-menu-item index="/admin/saleRecord" >
        销售记录
        </el-menu-item>
      <el-menu-item index="/admin/menuManage">
        菜单管理
      </el-menu-item>
    </el-menu>
    <router-view></router-view>
  </div>
</template>
<script>
  import SaleRecord from './SalesRecord'
  import MenuManage from './menuManage'
  export default {
    name: 'admin',
    data() {
      return {
        activeIndex: "/admin/saleRecord",
        api_key: null
      };
    },
    mounted () {
      this.api_key = this.$route.query.api_key
      this.$router.push({path: this.activeIndex, query: {api_key: this.api_key}})
    },
    methods: {
      handleSelect(path) {
        this.$router.push({path: path, query: {api_key: this.api_key}})
      }
    }
  }
</script>
<style rel="stylesheet/scss" lang="scss">
#container {
  margin: 0  2%;
}
</style>



