<template>
  <div class="login-container">

    <el-form class="login-form" autoComplete="on" :model="loginForm" :rules="loginRules" ref="loginForm"
             label-position="left">
      <div class="title-container">
        <h3 class="title">智眼销售管理系统</h3>
        <!-- <lang-select class="set-language"></lang-select> -->
      </div>
      <el-form-item prop="username">
        <span class="svg-container svg-container_login">
          <!-- <svg-icon icon-class="user" /> -->
        </span>
        <el-input name="username" type="text" v-model="loginForm.username" autoComplete="on" placeholder="用户名"
        />
      </el-form-item>

      <el-form-item prop="password">
        <span class="svg-container">
          <!-- <svg-icon icon-class="password" /> -->
        </span>
        <el-input name="password" :type="passwordType" @keyup.enter.native="handleLogin" v-model="loginForm.password"
                  autoComplete="on"
                  placeholder="密码"/>
        <span class="show-pwd" @click="showPwd">
          <!-- <svg-icon icon-class="eye" /> -->
        </span>
      </el-form-item>

      <el-button type="primary" style="width:100%;margin-bottom:30px;" :loading="loading"
                 @click.native.prevent="handleLogin">登陆
      </el-button>

      <div class="tips">
        <span>内测阶段</span>
        <!-- <span>{{$t('login.password')}} : {{$t('login.any')}}</span> -->
      </div>
      <!-- <div class="tips">
        <span style="margin-right:18px;">{{$t('login.username')}} : editor</span>
        <span>{{$t('login.password')}} : {{$t('login.any')}}</span>Y

      </div> -->

      <el-button class="thirdparty-button" type="primary" @click="showDialog=true">第三方</el-button>
    </el-form>

    <!-- <el-dialog title="第三方" :visible.sync="showDialog" append-to-body>
      第三方
      <br/>
      <br/>
      <br/>
      <social-sign />
    </el-dialog> -->

  </div>
</template>

<script>
  // import { isvalidUsername } from '@/utils/validate'
  // import LangSelect from '@/components/LangSelect'
  // import SocialSign from './socialsignin'
  import axios from "axios";
  import qs from "qs";

  export default {
    // components: { LangSelect, SocialSign },
    components: {},
    name: "Login",
    data() {
      const validateUsername = (rule, value, callback) => {
        // if (!isvalidUsername(value)) {
        //   callback(new Error('Please enter the correct user name'))
        // } else {
        //   callback()
        // }
        callback();
      };
      const validatePassword = (rule, value, callback) => {
        if (value.length < 1) {
          callback(new Error("The password can not be less than 1 digits"));
        } else {
          callback();
        }
      };
      return {
        loginForm: {
          username: "",
          password: ""
        },
        loginRules: {
          username: [
            {required: true, trigger: "blur", validator: validateUsername}
          ],
          password: [
            {required: true, trigger: "blur", validator: validatePassword}
          ]
        },
        passwordType: "password",
        loading: false,
        showDialog: false
      };
    },
    methods: {
      showPwd() {
        if (this.passwordType === "password") {
          this.passwordType = "";
        } else {
          this.passwordType = "password";
        }
      },
      handleLogin() {
        this.$refs.loginForm.validate(valid => {
          if (valid) {
            this.loading = true;

            const loginUrl = "http://121.41.92.64:7705/login/";
            this.$http
              .post(
                loginUrl,
                {
                  user_name: this.loginForm.username,
                  password: this.loginForm.password
                },
                {emulateJSON: true}
              )
              .then(function (res) {
                if (res.data.state_code === 0) {
                  let api_key = res.data.data.api_key;
                  this.loading = false;
                  this.$router.push({path: "/admin", query: {api_key}});
