<template>
	<div class="container">
	
		 <el-table
			 :data="currentRecordDetails"
			 style="width: 100%">
      <el-table-column
	      prop="name"
	      label="菜名"
	      width="180">
      </el-table-column>
      <el-table-column
	      prop="price"
	      label="价格"
	      width="180">
      </el-table-column>
    </el-table>
	</div>
</template>
<script>
	import qs from 'qs';
	export default {
	  data () {
	    return {
	      currentRecordDetails: null
	    }
	  },
    mounted () {
      let query = this.$route.query;
      this.api_key = query.api_key;
      this.record_id = query.record_id;
      console.log(query)
	    this.getDetails()
    },
		methods: {
	    getDetails () {
        this.$http
          .post(
            'http://121.41.92.64:7705/salerecord/detail/query/',
            qs.stringify({record_id: this.record_id}),
            {
              headers: {
                'api-key': this.api_key,
                'Content-Type': 'application/x-www-form-urlencoded'
              },
            },
            {emulateHTTP: true}
          )
          .then(res => {
            this.currentRecordDetails = res.body.data
          })
          .catch(e => {
          });
	    }
		}
  };
</script>
<style>

</style>



