<template>
  <div id="container">
    <el-table
	    :data='menuList'
	    border
	    fit
	    style="width:100%;text-align: center;"
	    center="all"
    >
      <el-table-column
	      v-cloak
	      style="display: none"
	      align="center"
	      fixed
	      label="菜品编号"
      >
      </el-table-column>
      <el-table-column
	      align="center"
	      fixed
	      prop="food_name"
	      label="菜名"
      >
        <template slot-scope="scope">
          <span v-if="!scope.row.editFlag">{{ scope.row.food_name }}</span>
          <span v-if="scope.row.editFlag">
            <el-input v-model="inputColumn.food_name" placeholder="请输入内容"></el-input>
          </span>
        </template>
      </el-table-column>

      <el-table-column
	      align="center"
	      fixed
	      label="价格"
      >
        <template slot-scope="scope">
          <span v-if="!scope.row.editFlag">{{ scope.row.food_price }}</span>
          <span v-if="scope.row.editFlag">
            <el-input v-model="inputColumn.food_price" placeholder="请输入内容"></el-input>
          </span>
        </template>
      </el-table-column>
      <el-table-column
	      align="center"
	      fixed
	      label="卡路里"
      >
        <template slot-scope="scope">
          <span v-if="!scope.row.editFlag">{{ scope.row.food_calorie }}</span>
          <span v-if="scope.row.editFlag">
            <el-input v-model="inputColumn.food_calorie" placeholder="请输入内容"></el-input>
          </span>
        </template>
      </el-table-column>
      <el-table-column
	      fixed
	      align="center"
	      label="风味"
      >
        <template slot-scope="scope">
          <span v-if="!scope.row.editFlag">{{ scope.row.food_special }}</span>
          <span v-if="scope.row.editFlag">
            <el-input v-model="inputColumn.food_special" placeholder="请输入内容"></el-input>
          </span>
        </template>
      </el-table-column>
      <el-table-column
	      fixed
	      align="center"
	      label="类型"
      >
        <template slot-scope="scope">
          <span v-if="!scope.row.editFlag">{{ scope.row.food_type }}</span>
          <span v-if="scope.row.editFlag">
            <el-input v-model="inputColumn.food_type" placeholder="请输入内容"></el-input>
          </span>
        </template>
      </el-table-column>
      <el-table-column
	      fixed
	      align="center"
	      label="成分"
      >
        <template slot-scope="scope">
          <span v-if="!scope.row.editFlag">{{ scope.row.food_element }}</span>
          <span v-if="scope.row.editFlag">
            <el-input v-model="inputColumn.food_element" placeholder="请输入内容"></el-input>
          </span>
        </template>
      </el-table-column>
      <el-table-column
	      fixed
	      align="center"
	      label="操作"
	      width="100"
      >
        <template slot-scope="scope">
          <el-button v-show="!scope.row.editFlag" @click="handleEdit(scope.row)" type="text" size="small">编辑</el-button>
          <el-button v-show="scope.row.editFlag" @click="handleUpload(scope)" type="text" size="small">上传</el-button>
          <el-button v-show="!scope.row.editFlag" @click="handleDelete(scope)" type="text" size="small">删除</el-button>
          <el-button v-show="scope.row.editFlag" @click="handleCancel(scope.row)" type="text" size="small">取消
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
	import qs from 'qs'
  export default {
    data () {
      return {
        api_key: '',
        host: 'http://121.41.92.64:7705/',
        inputColumn: {
          default: '请输入内容'
        },
        menuList: [
          {
            editFlag: false,
            food_name: '土豆牛肉',
            food_special: '123',
            food_type: '456',
            food_element: '789'
          }
        ]
      };
    },
    mounted () {
      this.api_key = this.$route.query.api_key;
    },
    methods: {
      handleEdit (row) {
        row.editFlag = true;
        console.log(row);
        this.inputColumn = {...this.inputColumn, ...row};
      },
      handleUpload (scope) {
        let row = scope.row;
        row.editFlag = false;
        this.menuList[scope.$index] = {...this.menuList[scope.$index], ...this.inputColumn};
        console.log(this.menuList[scope.$index]);
        this.addFood(this.menuList[scope.$index]);
      },
      handleDelete (row) {

      },
      handleCancel (row) {
        row.editFlag = false;
        this.inputColumn = {default: '请输入内容'};
      },
      // 上传菜单
      addFood (foodItem) {
        delete foodItem.editFlag;
        delete foodItem.default;
        foodItem = qs.stringify(foodItem)
	      console.log(foodItem)
        this.$http.post(`http://121.41.92.64:7705/menu/add/`,
          foodItem,
          {
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded',
              'api-key': this.api_key,
              'canteen-id': '9'
            }
          },
          {emulateJSON: true}
        ).then(res => {
          console.log(res);
        });
      }
    }
  };
</script>
<style>
  #container {
  }
  [v-cloak] {
	  display: none;
  }

</style>



