<template>
  <div class="app-container">
    <div class="filter-container">
      <el-select
	      clearable
	      style="width: 90px"
	      class="filter-item"
	      value-key="canteen_name"
	      v-model="listQuery.canteen"
	      placeholder="餐厅选择">
        <el-option
	        v-for="canteen in canteenList"
	        :key="canteen.canteen_id"
	        :label="canteen.canteen_name"
	        :value="canteen.canteen_name">
        </el-option>
      </el-select>
      <el-select
	      clearable
	      style="width: 90px"
	      class="filter-item"
	      value-key="machine_id"
	      v-model="listQuery.machine"
	      placeholder="机器选择">
        <el-option
	        v-for="machine in machineOptions"
	        :key="machine.machine_id"
	        :label="machine.machine_name"
	        :value="machine">
        </el-option>
      </el-select>
      <el-date-picker
	      v-model="listQuery.daterange"
	      type="daterange"
	      start-placeholder="开始日期"
	      end-placeholder="结束日期"
	      format="yyyy-MM-dd"
	      :default-time="['00:00:00', '23:59:59']">
      </el-date-picker>
      <el-select clearable style="width: 90px" class="filter-item" value-key="name" v-model="listQuery.meal"
                 placeholder="饭时选择">
        <el-option v-for="meal in mealOptions" :key="meal.index" :label="meal.name" :value="meal">
        </el-option>
      </el-select>
      <el-button class="filter-item" type="primary" v-waves icon="el-icon-search" @click="handleFilter">查询</el-button>
	    <el-button class="filter-item" type="primary" v-waves icon="el-icon-search" @click="AddRecord">添加记录</el-button>
      <el-button download="Acme Documentation (ver. 2.0.1).txt" class="filter-item" type="primary"
                 :loading="downloadLoading" v-waves icon="el-icon-download" @click="handleDownload">统计报表导出
      </el-button>
    </div>
    <el-table stripe :key='tableKey' :data="list" v-loading="listLoading" border fit highlight-current-row
              style="width:100%;text-align: center;"
              center="all">
      <el-table-column align="center" label="ID" width="180">
        <template slot-scope="scope">
          <span>{{scope.row.record_id}}</span>
        </template>
      </el-table-column>
      <el-table-column align="center" label="日期">
        <template slot-scope="scope">
          <span>{{scope.row.datetime | parseDate()}}</span>
        </template>
      </el-table-column>

      <el-table-column align="center" label="时间">
        <template slot-scope="scope">
          <span>{{scope.row.datetime | parseTime()}}</span>
        </template>
      </el-table-column>

      <el-table-column align="center" label="总价">
        <template slot-scope="scope">
          <span>{{scope.row.price_sum}}</span>
        </template>
      </el-table-column>

      <el-table-column align="center" label="详情">
        <template slot-scope="scope">
	        <el-button @click="handleDetails(scope.row)" type="primary" size="small">查看详情</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import waves from '@/directive/waves'; // 水波纹指令
  import axios from 'axios';
  import qs from 'qs';

  Date.prototype.addDays = function (days) {
    var date = new Date(this.valueOf());
    date.setDate(date.getDate() + days);
    return date;
  };

  function getDates (startDate, stopDate) {
    console.log(startDate, stopDate);
    startDate = startDate.addDays(1);
    stopDate = stopDate.addDays(1);
    var dateArray = new Array();
    var currentDate = startDate;
    while (currentDate <= stopDate) {
      dateArray.push(new Date(currentDate));
      currentDate = currentDate.addDays(1);
    }
    return dateArray;
  }

  export default {
    name: 'SalesRecord',
    directives: {
      waves
    },
    data () {
      return {
        currentRecordDetails: null,
        host: 'http://121.41.92.64:7705/',
        tableKey: 0,
        list: null,
        total: null,
        listLoading: true,
        listQuery: {
          page: 1,
          limit: 20,
          canteen: {},
          machine: {machine_id: -1, machine_name: '全部'},
          meal: {index: -1, name: '全部'},
          daterange: [new Date(new Date().toDateString()), new Date(new Date().toDateString())],
          type: undefined,
          sort: '+id'
        },
        canteenList: [],
        machineOptions: [{machine_id: -1, machine_name: '全部'}],
        mealOptions: [
          {index: -1, name: '全部'},
          {index: 1, name: '早饭'},
          {index: 2, name: '午饭'},
          {index: 3, name: '晚饭'}
        ],
        downloadLoading: false
      };
    },
    filters: {
      statusFilter (status) {
        const statusMap = {
          published: 'success',
          draft: 'info',
          deleted: 'danger'
        };
        return statusMap[status];
      },
      typeFilter (type) {
        return calendarTypeKeyValue[type];
      },
      parseDate (datetime) {
        return datetime.split(' ')[0];
      },
      parseTime (datetime) {
        return datetime.split(' ')[1];
      }
    },
    mounted () {
      /**
       * 从上个界面接收到的api_key
       */
      console.log('this.$route.params', this.$route.query);
      this.api_key = this.$route.query.api_key;
      console.log(this.api_key);
      this.getCanteenList();
      this.getMachineList();
    },
    methods: {
      handleDetails (row) {
        // console.log(row.record_id)
        
        this.$router.push({path: '/admin/recordDetails', query: {api_key: this.api_key, record_id: row.record_id}});
      },
      addRecord () {
        this.$http.post(
          'http://121.41.92.64:7705/salerecord/add_/',
          {
            headers: {
              'api-key': this.api_key,
              'Content-Type': 'application/x-www-form-urlencoded',
              'machine-id': this.listQuery.machine.machine_id
            }
          },
          {emulateJSON: true}
        ).then(res => {
          console.log(res);
        }).catch(e => {

        });
      },
      getCanteenList () {
        this.$http
          .get(
            `${this.host}canteen/list/`,
            {
              headers: {
                'api-key': this.api_key,
                'Content-Type': 'application/x-www-form-urlencoded'
              }
            },
            {emulateJSON: true}
          ).then(res => {
          console.log(res);
          this.canteenList = res.body.data.canteen;
        }).catch(e => {
          this.listLoading = false;
        });
      },
      getMachineList () {
        this.listLoading = true;
        this.$http
          .get(
            'http://121.41.92.64:7705/machine/list/',
            // {
            //     "sds":"sdsd"
            //   },
            {
              headers: {
                'api-key': this.api_key,
                'Content-Type': 'application/x-www-form-urlencoded',
                'canteen-id': '9'
              }
            },
            {emulateJSON: true}
          )
          .then(res => {
            this.listLoading = false;
            this.listLoading = true;
            let machines = (res.data.data);
            this.machineOptions = machines.concat({machine_id: -1, machine_name: '全部'});
            this.handleFilter();
          })
          .catch(e => {
            this.listLoading = false;
          });

      },
      handleFilter () {
        console.log('listQuery', this.listQuery);
        let {machine, meal, daterange} = this.listQuery;
        let machine_id_list = [];
        console.log(machine, this.machineOptions);
        if (machine.machine_id == -1) {
          machine_id_list = this.machineOptions.filter((machineO) => machineO.machine_id >= 0).map((machineO) => machineO.machine_id);
        } else {
          machine_id_list = [machine.machine_id];
        }
        console.log(machine_id_list);
        let meal_index_list = [];
        if (meal.index == -1) {
          meal_index_list = this.mealOptions.filter((mealO) => mealO.index >= 0).map((mealO) => mealO.index);
        } else {
          meal_index_list = [meal.index];
        }
        let dates = getDates(daterange[0], daterange[1]);
        daterange = dates.map((date) => date.toISOString().slice(0, 10));
        this.listLoading = true;
        console.log(this.listQuery.machine);
        this.$http
          .post(
            'http://121.41.92.64:7705/salerecord/query_/',
            qs.stringify({
              meal_filter: JSON.stringify(meal_index_list),
              date_filter: JSON.stringify(daterange),
              machine_filter: JSON.stringify(machine_id_list)
            }),
            {
              headers: {
                'api-key': this.api_key,
                'Content-Type': 'application/x-www-form-urlencoded'
              }
            },
            {emulateHTTP: true}
          )
          .then(res => {
            this.listLoading = false;
            this.list = res.body.data;
          })
          .catch(e => {
            this.listLoading = false;
          });
      },
      handleDownload () {
        console.log('listQuery');
        let {machine, meal, daterange} = this.listQuery;
        let machine_id_list = [];
        console.log(machine, this.machineOptions);
        if (machine.machine_id == -1) {
          machine_id_list = this.machineOptions.filter((machineO) => machineO.machine_id >= 0).map((machineO) => machineO.machine_id);
        } else {
          machine_id_list = [machine.machine_id];
        }
        console.log(machine_id_list);
        let meal_index_list = [];
        if (meal.index == -1) {
          meal_index_list = this.mealOptions.filter((mealO) => mealO.index >= 0).map((mealO) => mealO.index);
        } else {
          meal_index_list = [meal.index];
        }
        let dates = getDates(daterange[0], daterange[1]);
        daterange = dates.map((date) => date.toISOString().slice(0, 10));

        this.$http
          .post(
            'http://121.41.92.64:7705/salerecord/generate_sheet/',
            qs.stringify({
              meal_filter: JSON.stringify(meal_index_list),
              date_filter: JSON.stringify(daterange),
              machine_filter: JSON.stringify(machine_id_list)
            }),
            {
              responseType: 'blob',
              headers: {
                'api-key': this.api_key,
                'Content-Type': 'application/x-www-form-urlencoded'
              }
            },
            {emulateHTTP: true}
          )
          .then(res => {
            console.log('res', res);
            const content = res.bodyBlob;
            const blob = new Blob([content]);
            const elink = document.createElement('a');
            elink.download = '汇总表.xls';
            elink.style.display = 'none';
            elink.href = URL.createObjectURL(blob);
            document.body.appendChild(elink);
            elink.click();
            URL.revokeObjectURL(elink.href); // 释放URL 对象
            document.body.removeChild(elink);

            //          a = document.createElement('a');
            // a.href = window.URL.createObjectURL( res.body);
            // // Give filename you wish to download
            // a.download = "test-file.xls";
            // a.style.display = 'none';
            // document.body.appendChild(a);
            // a.click();
          })
          .catch(e => {
            this.listLoading = false;
          });
      },
      formatJson (filterVal, jsonData) {
        return jsonData.map(v =>
          filterVal.map(j => {
            if (j === 'timestamp') {
              return parseTime(v[j]);
            } else {
              return v[j];
            }
          })
        );
      }
    }
  };
</script>
<style scoped>
  .filter-container {
	  margin-top: 2%;
	  margin-left: 2%;
	  margin-bottom: 40px;
  }
</style>





